#trainingSlide {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.training-slider-container {
  width: 100%;
  position: relative;
}

.training-slider-sec {
  border-bottom: 3px solid #fdb649;
}

.training-slider-container .swiper {
  width: 100%;
  height: 500px;
  margin-left: auto;
  margin-right: auto;
}

.training-slider-container .swiper-slide {
  background-size: cover;
  background-position: center;
}

.training-swiper {
  width: 100%;
}

.mySwiperThumb {
  padding: 0px 0;
  height: 37% !important;
  position: absolute;
  bottom: 0px;
}

.mySwiperThumb .swiper-slide {
  width: 25%;
  height: 100%;
  transition: all 0.5s;
  transform: translateY(63%);
}

.mySwiperThumb .swiper-slide-thumb-active {
  transform: translateY(0%);
}

.training-slider-container .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-thumb-sec {
  border: 2px solid #fdb649;
  background-color: #fff;
  position: relative;
  padding: 1rem;
  cursor: pointer;
}

.slider-number-sec {
  background-color: #fdb649;
  color: #fff;
  height: 25px;
  width: 25px;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  font-weight: bold;
  font-family: "Poppins", sans-serif !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper-thumb-txt {
  padding: 1rem 1rem;
}

.swiper-thumb-heading p {
  color: #3680f7;
  font-family: "Poppins", sans-serif !important;
  font-weight: 600;
  font-size: 14px;
}

.swiper-thumb-heading {
  padding-bottom: 10px;
  border-bottom: 2px solid rgb(128 128 128 / 20%);
  margin-bottom: 10px;
}

.swiper-thumb-desc {
  padding-bottom: 10px;
  border-bottom: 2px solid rgb(128 128 128 / 20%);
  margin-bottom: 10px;
}

.swiper-thumb-desc p {
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
  color: rgb(84, 84, 84);
}

.swiper-thumb-txt .learn-more-button-sec button {
  background-color: transparent;
  color: rgb(84, 84, 84);
}

.swiper-thumb-txt .learn-more-button-sec {
  padding-bottom: 0;
}

.access-sec {
  margin-top: 1rem;
}

.access-sec p {
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
  color: #3680f7;
  cursor: pointer;
}

.access-sec p i {
  color: #fdb549dc;
  font-size: 14px;
  margin-left: 5px;
  transform: scale(-1);
}

.access-underline-animation {
  display: inline-block;
  position: relative;
  color: #fdb549dc;
}

.access-underline-animation::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #fdb549dc;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.access-underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
