#trainingOutdated {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.out-dated-inner-sec {
  width: 100%;
  position: relative;
}

.training-academy-logo {
  width: 55%;
  margin-left: -15px;
}

.out-dated-des-sec p {
  font-family: "Poppins", sans-serif !important;
  font-size: 18px;
  color: #000;
  padding: 1rem 0;
  letter-spacing: 0.5px;
}

.training-box-container {
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  padding-top: 4rem;
  padding-bottom: 2rem;
}

.out-dated-inner-sec .study-box {
  height: 100%;
  justify-content: space-evenly;
}

.out-dated-inner-sec .study-box p {
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
}

.out-dated-inner-sec .study-box span {
  font-size: 16px;
}

#DeliveryFormats {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.delivery-formate-sec {
  width: 100%;
  position: relative;
}

.delivery-box-sec {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.first-box-img-sec {
  background-image: url("../../assets/images/training/delivery.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.delivery-sec-box-sec {
  background-color: #fdb649;
  padding: 2rem 1rem 2rem 1rem;
  height: 200px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.delivery-middel-sec {
  background-color: #d9d9d9;
}

.delivery-sec-box-sec .story-heading-sec span {
  font-size: 18px;
}

.delivery-sec-box-sec .story-heading-sec p {
  font-family: "Poppins", sans-serif !important;
}

#engagementSec {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.engagement-inner-sec {
  position: relative;
  width: 100%;
}

.engagement-grid-items-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.engagement-grid-items-first p {
  font-family: "Poppins", sans-serif !important;
  font-size: 24px;
  width: 90%;
}

.engagement-inner-sec .stories-heading-sec {
  padding-bottom: 1.5rem;
}

.introduce-heading {
  font-family: "Poppins", sans-serif !important;
  font-size: 22px;
  border-bottom: 1px solid #fdb549dc;
  display: inline;
  padding-bottom: 0.5rem;
  margin-bottom: 2rem;
}

.introduce-three-grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 2rem;
}

.introduce-grid-item p {
  font-family: "Poppins", sans-serif !important;
  font-size: 18px;
  line-height: 1.2;
  border-bottom: 1px solid #fdb549dc;
  display: inline-table;
  padding-bottom: 0.5rem;
}

#trainingClient {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.training-client-logo-grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  margin-bottom: 6px;
}

.training-client-logo-grid-container .introduce-grid-item {
  text-align: center;
}

.pecb {
  width: 50%;
}

.dof {
  width: 48%;
}
.up {
  width: 46%;
}

.training-nyc {
  width: 45%;
}

.pb {
  width: 46%;
}

.vsu {
  width: 44%;
}
.pcw {
  width: 44%;
}

.pc {
  width: 38%;
}

.sjpef {
  width: 44%;
}
.cup {
  width: 44%;
}

.pagasa {
  width: 38%;
}

.gc {
  width: 100%;
}

#trainingConsultancyInsight {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.grayscal-img {
  filter: grayscale(1);
}

.grayscal-img:hover {
  filter: none;
  cursor: pointer;
}

.training-consultancy-inner-sec .story-heading-sec span {
  font-size: 17px;
  line-height: 1.2;
}

.training-consultancy-inner-sec .success-story-text-btn-sec p {
  font-size: 16px;
}

.consultancy-img-sec {
  background-image: url("../../assets/images/training/booking.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100%;
}

.mobile-training-grid-sec {
  display: none;
}

@media screen and (max-width: 480px) {
  .training-box-container {
    grid-template-columns: repeat(1, 1fr);
    gap: 5rem;
    padding-top: 3rem;
    padding-bottom: 0rem;
  }
  .mobile-training-grid-sec {
    display: block;
  }

  .mobile-training-slide-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .training-slider-sec {
    display: none;
  }

  .delivery-box-sec .first-box-img-sec {
    display: none;
  }

  .delivery-box-sec {
    grid-template-columns: repeat(1, 1fr);
  }

  .engagement-grid-items-container {
    display: grid;
    grid-template-columns: 1fr;
  }
  .engagement-grid-items-first p {
    font-family: "Poppins", sans-serif !important;
    font-size: 24px;
    width: 94%;
  }

  #trainingConsultancyInsight {
    padding-bottom: 5rem;
  }

  #trainingClient {
    padding-bottom: 5rem;
  }

  #engagementSec {
    padding-bottom: 5rem;
  }

  #DeliveryFormats {
    padding-bottom: 5rem;
  }
}
