#CustomerSuccessStrory {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.customer-strory-inner-sec {
  width: 100%;
  position: relative;
}

.cutomer-strory-heading-sec {
  border-bottom: 2px solid rgba(128, 128, 128, 0.366);
  padding-bottom: 1rem;
}

.cutomer-strory-heading-sec p {
  font-weight: 700;
  letter-spacing: 1px;
  display: inline-block;
  background: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 30px;
  font-family: "Poppins", sans-serif !important;
}

.succes-story-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  margin-top: 3rem;
}

.first-story-sec {
  display: grid;
  grid-template-columns: 1fr 0.8fr;
  align-items: center;
}

.story-content-sec {
  background-color: #42d7f1;
  padding: 2rem 1rem 2rem 1rem;
  height: 260px;
}

.story-heading-sec {
  border-bottom: 2px solid rgba(128, 128, 128, 0.366);
  padding-bottom: 1.5rem;
}

.story-heading-sec span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700;
  color: #000;
  font-size: 22px;
  text-transform: uppercase;
}

.success-story-text-btn-sec {
  padding-top: 1rem;
}

.success-story-text-btn-sec p {
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
  color: #000;

  padding-bottom: 1rem;
}

.success-story-text-btn-sec button {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
  border: none;
  background: transparent;
  padding: 0;
  padding-top: 1rem;
  cursor: pointer;
}

.story-img-sec {
  background-image: url("../../assets/images/case-study/read-use-case.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
}
.second-story-img {
  background-image: url("../../assets/images/case-study/read-use-case2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
}

.university-img-sec {
  background-image: url("../../assets/images/case-study/upd.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
}

.vsu-img-sec {
  background-image: url("../../assets/images/case-study/vsu.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  height: 100%;
}
.pamana-img-sec {
  background-image: url("../../assets/images/case-study/pmc.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100%;
}

#localGovern {
  width: 100%;
  position: relative;
  padding-bottom: 10rem;
  overflow: hidden;
}

.local-government-inner-sec {
  width: 100%;
  position: relative;
}

.local-govt-img {
  background-image: url("../../assets/images/case-study/local-govt-img.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100%;
}

.local-government-inner-sec .story-content-sec {
  background-color: #fada4f;
  padding: 3rem 2rem 3rem 2rem;
  background-image: url("../../assets/images/case-study/case-bg2.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
}

#storyBoxSec {
  width: 100%;
  position: relative;
  padding-bottom: 5rem;
  overflow: hidden;
}
.study-box-container {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  align-items: center;
}

.study-box p {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700;
  color: #000;
  font-size: 18px;
  border-bottom: 2px solid rgba(128, 128, 128, 0.366);
  padding-bottom: 1rem;
  margin-bottom: 1rem;
  height: 50px;
}

.study-box {
  border: 2px solid rgba(128, 128, 128, 0.213);
  padding: 1.5rem;
  height: 78%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 6px;
}

.study-box:hover {
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
  background: #f5f6f9;
}

.study-box span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  color: #000;
  font-size: 12px;
  line-height: 1.3;
}

.study-box button {
  font-size: 12px;
  color: #3680f7;
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  font-family: "Poppins", sans-serif !important;
  font-size: 14px;
  border: none;
  background: transparent;
  padding: 0;
  padding-top: 2rem;
  cursor: pointer;
}

.box-bg1 {
  background-image: url("../../assets/images/case-study/1.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.box-bg2 {
  background-image: url("../../assets/images/case-study/2.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.box-bg3 {
  background-image: url("../../assets/images/case-study/3.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.box-bg4 {
  background-image: url("../../assets/images/case-study/4.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media screen and (max-width: 480px) {
  #CustomerSuccessStrory {
    padding-bottom: 5rem;
  }

  .succes-story-container {
    grid-template-columns: 1fr;
  }

  .first-story-sec {
    grid-template-columns: 1fr;
  }

  .study-box-container {
    grid-template-columns: repeat(2, 1fr);
  }

  .story-content-sec {
    height: 235px;
    padding: 1rem;
  }

  .study-box p {
    height: 70px;
  }
}

@media screen and (max-width: 360px) {
  .study-box p {
    height: 94px;
  }

  #localGovern {
    padding-bottom: 5rem;
  }

  .study-box {
    padding: 0.8rem;
    height: 88%;
    padding-top: 1rem;
  }

  .local-government-inner-sec .story-content-sec {
    padding: 2rem;
    height: 290px;
  }
}
